import uniStatusBar from '@/common/uni-status-bar/uni-status-bar.vue';
import otherLogin from '@/common/common/other-login.vue';

export default {
	components: {
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			tabbarColor: '#FFE933',
			username: '',
			psd: '',
			isTel: true,
			telephone: '',
			yzm: '',
			yzmtext: '获取验证码',
			yzmTime: false,
			providerList:this.$store.state.providerList
			// isyzm:true?
		};
	},
	onLoad() {
		
	},
	computed: {
		//手机号码验证码登录
		isyzm() {
			const mPattern = /^1[34578]\d{9}$/;
			if (this.telephone && mPattern.test(this.telephone)) {
				return false;
			}
			return true;
		},
		
		yzmCheck() {
			if (!this.isyzm && !this.yzmTime) {
				return false;
			}
			return true;
		},

		hasLogin() {
			if(this.isTel){
				if (!this.isyzm && this.yzm && this.yzm.length == 6) {
					return false;
				}
				return true;
			}else{
				if(this.username && this.psd){
					return false
				}
				return true;
			}
		}
	},
	methods: {
		colseLogin() {
			console.log('close');
			uni.navigateBack({
				delta:1
			})
		},
		changeTel(num) {
			if (num == 2) {
				this.isTel = false;
			} else if (num == 1) {
				this.isTel = true;
			}
		},
		//获取验证码
		getyzm() {
			console.log('验证码');
			this.yzmTime = true;
			let i = 60;
			let timer = setInterval(() => {
				this.yzmtext = '获取验证码' + i + 's';
				i--;
				if (i < 0) {
					this.yzmTime = false;
					this.yzmtext = '获取验证码';
					clearInterval(timer);
				}
			}, 1000);
			console.log('验证码获取完毕')
		}
	}
};
